import React, { useState } from 'react';
import NPCDialog from './NPCDialog';
import './HeaderNav.css';
import logo from '../assets/images/me-pixel-head.png';

function HeaderNav({ setActiveSection }) {
  const [menuOpen, setMenuOpen] = useState(false);
  const [npcMessage, setNpcMessage] = useState('');
  const [dialogTrigger, setDialogTrigger] = useState(0);

  // NPC messages for each section
  const npcMessages = {
    landing: "Oh, welcome back.",
    about: "Ah, want to know my story? Eh?",
    skills: "Check out these skills I have!",
    contact: "Let's build something amazing together!"
  };

  const handleGoBack = () => {
    setActiveSection('landing');
    setMenuOpen(false);
    // Trigger NPC dialog
    setNpcMessage(npcMessages.landing);
    setDialogTrigger(prev => prev + 1);
  };

  const handleNavClick = (section) => {
    setActiveSection(section);
    setMenuOpen(false);
    // Trigger NPC dialog
    setNpcMessage(npcMessages[section] || "Thanks for clicking! ");
    setDialogTrigger(prev => prev + 1);
  };

  return (
    <div>
      <nav className="header-nav">
        <div className="logo-container" onClick={handleGoBack}>
          <img src={logo} alt="Engel Gatus logo" className="logo" />
        </div>

        {/* Desktop + mobile menu container */}
        <ul className={`nav-links${menuOpen ? ' open' : ''}`}>
          <li onClick={() => handleNavClick('about')}>About</li>
          <li onClick={() => handleNavClick('skills')}>What I am capable of</li>
          <li onClick={() => handleNavClick('contact')}>Contact</li>
        </ul>

        {/* Mobile-only burger */}
        <button
          className={`burger${menuOpen ? ' open' : ''}`}
          onClick={() => setMenuOpen(o => !o)}
          aria-label="Toggle navigation"
        >
          <span />
          <span />
          <span />
        </button>
      </nav>

      {/* NPC Dialog - positioned relative to your head logo */}
      <NPCDialog 
        trigger={dialogTrigger} 
        message={npcMessage} 
      />
    </div>
  );
}

export default HeaderNav;
