import { useState, useEffect, useRef, useCallback } from 'react';
import './CustomCursor.css';

function CustomCursor() {
  const [isHovering, setIsHovering] = useState(false);
  const [isGlitchMode, setIsGlitchMode] = useState(false); // NEW: Glitch mode state
  const [currentColorIndex, setCurrentColorIndex] = useState(0);
  const [trails, setTrails] = useState([]);
  const cursorRef = useRef(null);
  const rafRef = useRef(null);
  const mousePos = useRef({ x: 0, y: 0 });
  const currentPos = useRef({ x: 0, y: 0 });
  const prevPos = useRef({ x: 0, y: 0 });
  const lastTrailTime = useRef(0);
  const trailIdCounter = useRef(0);
  
  const cursorColors = [
    '#c2a85d', // Default accent
    '#FF6B9D', // Hot Pink
    '#FFE55C', // Bright Gold  
    '#4CAF50', // Vibrant Green
    '#2196F3', // Electric Blue
    '#FF8A65', // Coral Orange
    '#BA68C8', // Purple
    '#FFD700', // Gold
    '#FF4444', // Red
    '#00E676', // Neon Green
    '#00B8FF', // Cyan
  ];

  //  NEW: Listen for glitch mode activation
  useEffect(() => {
    const handleGlitchMode = (e) => {
      setIsGlitchMode(e.detail.isActive);
      if (!e.detail.isActive) {
        setCurrentColorIndex(0); // Reset to default when glitch ends
      }
    };

    window.addEventListener('glitchModeChange', handleGlitchMode);
    return () => window.removeEventListener('glitchModeChange', handleGlitchMode);
  }, []);

  // Color cycling on hover OR glitch mode
  useEffect(() => {
    let colorInterval;
    
    if (isHovering || isGlitchMode) {
      colorInterval = setInterval(() => {
        setCurrentColorIndex((prevIndex) => 
          (prevIndex + 1) % cursorColors.length
        );
      }, isGlitchMode ? 150 : 200); // Faster cycling in glitch mode
    } else {
      setCurrentColorIndex(0);
    }
    
    return () => {
      if (colorInterval) clearInterval(colorInterval);
    };
  }, [isHovering, isGlitchMode, cursorColors.length]);
  
  // Near-native speed with minimal trail
  const updateCursor = useCallback(() => {
    if (!cursorRef.current) return;
    
    const ease = 0.35;
    prevPos.current.x = currentPos.current.x;
    prevPos.current.y = currentPos.current.y;
    
    currentPos.current.x += (mousePos.current.x - currentPos.current.x) * ease;
    currentPos.current.y += (mousePos.current.y - currentPos.current.y) * ease;
    
    const transform = `translate3d(${currentPos.current.x}px, ${currentPos.current.y}px, 0) translate(-50%, -50%)`;
    cursorRef.current.style.transform = transform;
    
    // Generate minimal trail
    const now = Date.now();
    const timeSinceLastTrail = now - lastTrailTime.current;
    
    const velocityX = currentPos.current.x - prevPos.current.x;
    const velocityY = currentPos.current.y - prevPos.current.y;
    const speed = Math.sqrt(velocityX * velocityX + velocityY * velocityY);
    
    const isMoving = speed > 0.3;
    
    if (isMoving && timeSinceLastTrail > 50) {
      const dirX = velocityX / (speed || 1);
      const dirY = velocityY / (speed || 1);
      
      const depth = 8;
      const coneWidth = depth * 0.6;
      
      const perpX = -dirY;
      const perpY = dirX;
      const scatter = (Math.random() - 0.5) * coneWidth;
      
      const newTrail = {
        id: trailIdCounter.current++,
        x: currentPos.current.x - (dirX * depth) + (perpX * scatter),
        y: currentPos.current.y - (dirY * depth) + (perpY * scatter),
        color: cursorColors[currentColorIndex],
        createdAt: now,
      };
      
      setTrails(prev => [...prev, newTrail]);
      lastTrailTime.current = now;
    }
    
    rafRef.current = requestAnimationFrame(updateCursor);
  }, [currentColorIndex, cursorColors]);

  // Clean up old trail particles
  useEffect(() => {
    const cleanupInterval = setInterval(() => {
      const now = Date.now();
      setTrails(prev => prev.filter(trail => now - trail.createdAt < 400));
    }, 50);
    
    return () => clearInterval(cleanupInterval);
  }, []);

  useEffect(() => {
    const updateMousePos = (e) => {
      mousePos.current.x = e.clientX;
      mousePos.current.y = e.clientY;
    };

    const handleMouseEnter = () => setIsHovering(true);
    const handleMouseLeave = () => setIsHovering(false);

    rafRef.current = requestAnimationFrame(updateCursor);

    document.addEventListener('mousemove', updateMousePos, { passive: true });

    const interactiveElements = document.querySelectorAll(
      'a, button, [role="button"], input, textarea, select, .clickable, .name-easter-egg'
    );

    interactiveElements.forEach(el => {
      el.addEventListener('mouseenter', handleMouseEnter, { passive: true });
      el.addEventListener('mouseleave', handleMouseLeave, { passive: true });
    });

    return () => {
      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current);
      }
      
      document.removeEventListener('mousemove', updateMousePos);
      interactiveElements.forEach(el => {
        el.removeEventListener('mouseenter', handleMouseEnter);
        el.removeEventListener('mouseleave', handleMouseLeave);
      });
    };
  }, [updateCursor]);

  const currentColor = cursorColors[currentColorIndex];

  return (
    <>
      {/* Minimal trail particles */}
      {trails.map(trail => (
        <div
          key={trail.id}
          className="cursor-trail"
          style={{
            left: `${trail.x}px`,
            top: `${trail.y}px`,
            backgroundColor: trail.color,
            boxShadow: `0 0 6px ${trail.color}`,
          }}
        />
      ))}

      {/* Main cursor dot */}
      <div 
        ref={cursorRef}
        className={`custom-cursor ${isHovering ? 'hovering' : ''}`}
        style={{
          '--cursor-color': currentColor,
        }}
      >
        <div className="cursor-inner"></div>
      </div>
    </>
  );
}

export default CustomCursor;
